.home{
	max-width: 1500px;
	background-color: lightgrey;
	margin-right: auto;
	margin-left: auto;
}
.home_image{
	background-image: linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)),url(data:image/jpeg;base64,);
	background-size: cover;
	background-position: center;
	width: 100%;
	height: 300px;
	text-align: center;
	background-color: white;
	mask-image: linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,0));
	margin-bottom: -30px;
	z-index: -1;
}
.home_image p{
	padding-top: 40px;
	color: white;
	letter-spacing: 15px;
	font-size: 60px;
	font-weight: 200px;
}
.home_image p span{
	font-size: 35px;
	/*letter-spacing: 0px;*/
}
.home_row{
	display: flex;
	z-index: 1;
	margin-left: 5px;
	margin-right: 5px;
}

